.summerAnimationColor{
    /* background: linear-gradient(270deg, #81caff, #419fe1); */
    background: -webkit-gradient(linear, right top, left top, from(#93d4ff), to(#69bff8));
    background: -webkit-linear-gradient(right, #93d4ff, #69bff8);
    background: -o-linear-gradient(right, #93d4ff, #69bff8);
    background: linear-gradient(270deg, #93d4ff, #69bff8);
}
.winterAnimationColor{
    background: -webkit-gradient(linear, right top, left top, from(#e8ebed), to(#b6cede));
    background: -webkit-linear-gradient(right, #e8ebed, #b6cede);
    background: -o-linear-gradient(right, #e8ebed, #b6cede);
    background: linear-gradient(270deg, #e8ebed, #b6cede);
}
.foggyAnimationColor{
    background: -webkit-gradient(linear, right top, left top, from(#707579), to(#585858));
    background: -webkit-linear-gradient(right, #707579, #585858);
    background: -o-linear-gradient(right, #707579, #585858);
    background: linear-gradient(270deg, #707579, #585858);
}
.rainyAnimationColor{
    background: -webkit-gradient(linear, right top, left top, from(#63869d), to(rgb(26, 55, 74)));
    background: -webkit-linear-gradient(right, #63869d, rgb(26, 55, 74));
    background: -o-linear-gradient(right, #63869d, rgb(26, 55, 74));
    background: linear-gradient(270deg, #63869d, rgb(26, 55, 74));
}



.bgAnimation{
    width: 100%;
    height: 100%;
    background-size: 400% 400%;
    -webkit-animation: AnimationName 5s ease infinite;
    animation: AnimationName 5s ease infinite;
}

@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}


